"use client";

import { useEffect, useState } from "react";
import api from "@/utils/apiAuth";
import { useRouter } from "next/navigation";

export default function Profile() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    const token = localStorage.getItem("token");

    // Se não tiver token → manda pro login
    if (!token) {
      router.push("/login");
      return;
    }

    // Busca o perfil no backend
    const fetchProfile = async () => {
      try {
        const res = await api.get("/profile", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setUser(res.data.user);
      } catch (err) {
        console.error(err);
        router.push("/login");
      }
    };

    fetchProfile();
  }, [router]);

  if (!user) {
    return <p className="text-white text-center mt-10">Carregando...</p>;
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center text-black">
      <h1 className="text-4xl font-bold">Bem-vindo, {user.name}</h1>
      <p className="text-lg mt-3">{user.email}</p>
    </div>
  );
}
